	module.exports = {"data":{"post":{"html":"<p>I don't mention Mama or her employer (a firm I'm sure you've heard of) by name here anymore, since she wants to avoid being googled. But this afternoon she opened a location in the next town to where we live; silently, so to speak, since her ad in the local paper said the the opening would be tomorrow. And they were still overrun by customers in the first hour. So she's very happy, and she won't mind going back tomorrow to help out, even though it's a holiday.</p>","fields":{"title":"Right Next Door","date":"30 Apr 2003","url":"https://papascott.de/2003-04-30-right-next-door/"}}},"pathContext":{"slug":"/2003-04-30-right-next-door/","prev":{"fields":{"title":"My bad...what?","date":"30 Apr 2003","slug":"/2003-04-30-my-badwhat/"}},"next":{"fields":{"title":"Science and literature","date":"30 Apr 2003","slug":"/2003-04-30-science-and-literature/"}}}}

